"use client";

import { useState } from "react";
import Link from "next/link";
import { useRouter } from "next/navigation";

export default function LoginPage() {
  const router = useRouter();
  const [phone, setPhone] = useState("");
  const [password, setPassword] = useState("");
  const [remember, setRemember] = useState(false);

  const handleLogin = (e: React.FormEvent) => {
    e.preventDefault();

    // ðŸ‘‰ contoh simple validasi (bisa diganti dengan fetch API ke backend)
    if (phone === "08123456789" && password === "123456") {
      alert("Login sukses!");
      router.push("/dashboard"); // arahkan ke halaman dashboard
    } else {
      alert("Nomor HP atau Password salah!");
    }
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen px-4">
      {/* Tombol kembali ke halaman utama */}
      <div className="w-full max-w-sm mb-4">
        <button
          onClick={() => router.push("/")}
          className="px-4 py-2 bg-black text-white font-semibold rounded-lg"
        >
          Kembali
        </button>
      </div>

      {/* Logo */}
      <img
        src="/logo-sidera.png"
        alt="Logo"
        className="w-32 h-32 mb-6"
      />

      {/* Form Login */}
      <form
        onSubmit={handleLogin}
        className="w-full max-w-sm bg-white p-6 rounded-2xl shadow-lg"
      >
        <div className="mb-4">
          <input
            type="text"
            placeholder="Nomor HP"
            value={phone}
            onChange={(e) => setPhone(e.target.value)}
            className="w-full border rounded-md px-4 py-2 focus:outline-none"
          />
        </div>

        <div className="mb-4">
          <input
            type="password"
            placeholder="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="w-full border rounded-md px-4 py-2 focus:outline-none"
          />
        </div>

        <div className="flex items-center mb-4">
          <input
            type="checkbox"
            checked={remember}
            onChange={() => setRemember(!remember)}
            className="mr-2"
          />
          <label>Ingat username saya!</label>
        </div>

        <button
          type="submit"
          className="w-full bg-green-600 text-white font-semibold rounded-md px-4 py-2 hover:bg-green-700"
        >
          Masuk
        </button>

        <div className="mt-6 text-center text-sm">
          <p>
            Belum punya akun?{" "}
            <Link href="/signup" className="text-orange-600 font-semibold">
              Daftar
            </Link>
          </p>
          <p>
            Lupa Password?{" "}
            <Link href="/reset-password" className="text-orange-600 font-semibold">
              Klik
            </Link>
          </p>
        </div>
      </form>
    </div>
  );
}
